import fetch from '../fetch'

/**
 * App service
 */
const App = {
  page: (user, param) => fetch({
    url: '/api/manage/app/page',
    method: 'post',
    data: {user: user, param}
  }),
  managers: (id) => fetch({
    url: '/api/manage/app/managers/' + id,
    method: 'get',
  }),
};

export default App