<template>
  <div class="user-table-select">
    <el-dialog
        title="选择Api"
        :visible.sync="visible"
        :before-close="doCancel">
      <div class="apis">
        <a class="api" v-for="(api, index) in apis" :key="api.id" @click.stop="doSelect(index)">
          <div class="-icon">{{api.storeName}}</div>
          <div class="-info">
            <div class="-ops">
              <span class="-name">{{api.name}}</span>
              <span class="-price"><span class="-p1">￥</span><span class="-p2">{{api.price}}</span><span
                  class="-p3">/年</span></span>
            </div>
            <div class="-desc">{{api.descs}}</div>
          </div>
        </a>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import ServiceApi from '@/api/system/api';

  export default {
    name: "apis-table",
    data() {
      return {
        apis: []
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    mounted() {
      this.loadApis();
    },
    methods: {
      loadApis: function () {
        ServiceApi.list().then(res => {
          this.apis = res.obj || [];
        })
      },
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      doSelect: function (index) {
        var api = this.apis[index];
        this.$emit('select:api', api)
        this.$emit('update:visible', false)
      },
    }
  }
</script>

