<template>
  <div class="user-table-select">
    <el-dialog
        title="选择用户"
        :visible.sync="visible"
        :before-close="doCancel">
      <el-form :inline="true" :model="query">
        <el-form-item label="姓名">
          <el-input v-model="query.name" placeholder="姓名"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="doQuery">查询</el-button>
        </el-form-item>
      </el-form>
      <el-table ref="usersGrid" :data="users" border v-loading="loading" highlight-current-row
                @current-change="doSelect">
        <el-table-column property="name" label="用户名称"></el-table-column>
        <el-table-column property="type" label="用户类型"></el-table-column>
        <el-table-column property="createDate" label="注册日期"></el-table-column>
      </el-table>
      <nav class="pagination">
        <el-pagination
            background
            layout="pager"
            :total="this.param.recordCount" :page-size="this.param.pageSize"
            @current-change="changePage">
        </el-pagination>
      </nav>
    </el-dialog>
  </div>
</template>

<script>
  import UserApi from '@/api/system/app';

  export default {
    name: "user-table",
    data() {
      return {
        loading: true,
        query: {},
        users: [],
        param: {}
      }
    },
    props: {
      visible: {
        type: Boolean,
        default: false
      }
    },
    mounted() {
      this.page(1)
    },
    methods: {
      page: function (index) {
        this.loading = true;
        this.param.pageIndex = index;
        UserApi.page(this.query, this.param).then(res => {
          this.users = res.obj.data;
          this.param = res.obj.param;
          this.loading = false;
        })
      },
      doCancel: function () {
        this.$emit('update:visible', false)
      },
      doQuery: function () {
        this.page(1)
      },
      doSelect: function (row) {
        this.$emit('select:user', row)
        this.$emit('update:visible', false)
      },
      changePage: function () {
      
      }
    }
  }
</script>

