<template>
  <div class="tax-page param-add">
    <h4>API订阅<span>/</span>{{this.subscribe.id?'修改':'添加'}}订阅</h4>
    <div class="content">
      <el-form ref="inputForm" :model="subscribe" :rules="rules" label-width="120px">
        <el-form-item label="订阅用户" prop="userName">
          <el-input v-model="subscribe.userName" placeholder="请选择订阅用户">
            <template slot="append">
              <el-button @click="doSelectUser">选择</el-button>
            </template>
          </el-input>
          <div class="form-tip">请选择要订阅的用户.</div>
        </el-form-item>
        <el-form-item label="订阅服务" prop="apiName">
          <el-input v-model="subscribe.apiName" placeholder="请选择订阅服务">
            <template slot="append">
              <el-button @click="doSelectApi">选择</el-button>
            </template>
          </el-input>
          <div class="form-tip">请选择要订阅的服务.</div>
        </el-form-item>
        <el-form-item label="开始时间" prop="beginDate">
          <el-date-picker v-model="subscribe.beginDate" type="date" placeholder="请设置开始时间"></el-date-picker>
          <div class="form-tip">设置订阅的开始时间，不填写则自动设置为当天.</div>
        </el-form-item>
        <el-form-item label="结束时间" prop="endDate">
          <el-date-picker v-model="subscribe.endDate" type="date" placeholder="请设置结束时间"></el-date-picker>
          <div class="form-tip">设置订阅的结束时间，到期之后不能继续使用服务 .</div>
        </el-form-item>
        <el-form-item label="支付状态" prop="payState">
          <el-radio v-model="subscribe.payState" label="UN_PAY">未支付</el-radio>
          <el-radio v-model="subscribe.payState" label="PAYED">已支付</el-radio>
          <el-radio v-model="subscribe.payState" label="FREE">免费</el-radio>
          <div class="form-tip">设置支付状态.</div>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit">提交</el-button>
          <swak-link class="el-button" to="/system/subs">返回</swak-link>
        </el-form-item>
      </el-form>
    </div>
    <user-table :visible.sync="userSelectable" @select:user="userSelected"/>
    <apis-table :visible.sync="apiSelectable" @select:api="apiSelected"></apis-table>
  </div>
</template>

<script>
  import UserTable from "../../ui/components/user/userTable";
  import ApisTable from "../../ui/components/api/apiTable";
  import SubsApi from '@/api/system/subs';

  export default {
    components: {
      ApisTable,
      UserTable
    },
    name: "subs-save",
    data() {
      return {
        subscribe: {},
        userSelectable: false,
        apiSelectable: false,
      }
    },
    mounted() {
      this.$emit('layout:load', "subs")
      var id = this.$route.params.id;
      this.loadSub(id);
    },
    methods: {
      loadSub: function (id) {
        if (!id) {
          return;
        }
        SubsApi.get(id).then(res => {
          this.subscribe = res.obj || {};
        });
      },
      onSubmit: function () {
        this.$refs['inputForm'].validate(res => {
          if (res && this.subscribe.userId && this.subscribe.apiId) {
            SubsApi.save(this.subscribe).then(data => {
              this.$notify.success("保存订阅成功!");
              this.$router.replace("/system/subs");
            });
          } else {
            this.$notify.error("表单错误，请输入必填项目");
          }
        })
      },
      doSelectUser: function () {
        this.userSelectable = true;
      },
      userSelected: function (user) {
        if (user) {
          this.subscribe.userId = user.id;
          this.subscribe.userName = user.name;
        }
      },
      doSelectApi: function () {
        this.apiSelectable = true;
      },
      apiSelected: function (api) {
        if (api) {
          this.subscribe.apiId = api.id;
          this.subscribe.apiName = api.name;
        }
      },
    }
  }
</script>

