import fetch from '../fetch'

/**
 * payitem service
 */
const Subs = {
  get: (id) => fetch({
    url: '/api/manage/subscribe/get/' + id,
    method: 'get'
  }),
  page: (subscribe, param) => fetch({
    url: '/api/manage/subscribe/page',
    method: 'post',
    data: {
      subscribe: subscribe,
      param: param
    }
  }),
  save: (data) => fetch({
    url: '/api/manage/subscribe/save',
    method: 'post',
    data: data
  }),
  delete: (id) => fetch({
    url: '/api/manage/subscribe/delete',
    method: 'post',
    data: id
  }),
  renewal: (data) => fetch({
    url: '/api/manage/subscribe/renewal',
    method: 'post',
    data: data
  }),
};

export default Subs